import { SingletonService } from './../services/singleton.service';
import { ProgressBarComponent } from './../progress-bar/progress-bar.component';
import {
    Input,
    Injectable,
    Component,
    OnInit,
    HostListener,
    EventEmitter,
    Output,
    ViewChild,
    TemplateRef, OnChanges,
    ChangeDetectionStrategy
} from '@angular/core';
import { CommonModule, registerLocaleData } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import {
    startOfDay,
    endOfDay,
    subDays,
    addDays,
    endOfMonth,
    isSameDay,
    isSameMonth,
    addHours
} from 'date-fns';
import { Subject } from 'rxjs/Subject';

import {
    CalendarEvent,
    CalendarEventAction
} from 'angular-calendar';

import { APIv3Service } from '../services/api3.service';
import { BookableItem } from '../entity/BookableItem';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';


@Component({
    selector: 'app-calendar',
    templateUrl: './calendar.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush,
    styleUrls: ['./calendar.component.css'],
})

@Injectable()
export class CalendarComponent implements OnInit {
    @Output() eventSelected = new EventEmitter<{ currentAction: number, event: Event }>();
    activeDayIsOpen = true;
    view = 'month';
    viewType = 'calendar';
    viewDate: Date;
    refresh: Subject<any> = new Subject();
    events: CalendarEvent[] = [];

    bookableItems: BookableItem[] = [];
    selectedBookableItem: BookableItem;

    weekStartsOn: string;
    languageCultureShortName: string;
    languageShortName: string;
    sagendaTranslationNext: string;
    sagendaTranslationPrevious: string;
    sagendaTranslationToday: string;
    sagendaTranslationMonth: string;
    sagendaTranslationWeek: string;
    sagendaTranslationDay: string;
    sagendaDateFormat: string;
    sagendaTimeFormat: string;
    bearerToken: string;


    /**
     * Constructor
     */
    constructor(private apiv3Service: APIv3Service, private progressBarComponent: ProgressBarComponent, private singletonService: SingletonService) {
        this.weekStartsOn = this.getWeekStartsOn();
        this.languageShortName = this.getLanguageShortName();
        this.languageCultureShortName = this.getLanguageCultureShortName();
        this.sagendaDateFormat = _window().sagendaDateFormat;
        this.sagendaTimeFormat = _window().sagendaTimeFormat;

        // translations
        this.sagendaTranslationNext = _window().sagendaTranslationNext;
        this.sagendaTranslationToday = _window().sagendaTranslationToday;
        this.sagendaTranslationPrevious = _window().sagendaTranslationPrevious;
        this.sagendaTranslationMonth = _window().sagendaTranslationMonth;
        this.sagendaTranslationWeek = _window().sagendaTranslationWeek;
        this.sagendaTranslationDay = _window().sagendaTranslationDay;
        this.progressBarComponent = progressBarComponent;

        this.singletonService.viewDate.subscribe(
            (viewDate: Date) => { this.viewDate = viewDate; }
        );

        this.singletonService.bearerToken.subscribe(
            (bearerToken: string) => { this.bearerToken = bearerToken; }
        );
    }

    /**
     * By clicking on "Previous / Today / Next" buttons we refresh the events.
     */
    OnChangePreviousTodayNext() {
        this.getEvents();
        this.singletonService.setViewDate(this.viewDate);
    }

    /**
     * By changing the bookable item in the list we set the newly selected item and refresh events.
     */
    OnChangeBookableItem(item: BookableItem) {
        this.selectedBookableItem = item;
        this.getEvents();
    }

    ngOnInit() {
        this.apiv3Service.getBearerToken(_window().sagendaToken).subscribe(
            (objects) => {
                this.bearerToken = objects;
                this.getBookableItems(this.bearerToken);
                this.singletonService.setBearerToken(objects);
            });
    }

    /**
     * Get events
     */
    getEvents(): void {
        this.progressBarComponent.displayProgressBar(true);
        this.apiv3Service.getEvents(this.viewDate,
            this.selectedBookableItem,
            this.sagendaDateFormat,
            this.sagendaTimeFormat,
            this.languageCultureShortName,
            this.bearerToken
        ).subscribe(objects => {
            this.events = objects;
            this.refresh.next();
            this.progressBarComponent.displayProgressBar(false);
        });
    }

    /**
     * Get bookable items list
     */
    getBookableItems(bearerToken): void {
        this.apiv3Service.getBookableItems(bearerToken).subscribe(objects => {
            this.bookableItems = objects;
            this.selectedBookableItem = objects[0];
            this.getEvents();
        });
    }

    /**
     * Action on event click
     */
    dayClicked({ date, events }: { date: Date, events: CalendarEvent[] }): void {
        if (isSameMonth(date, this.viewDate)) {
            if ((
                isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
                events.length === 0) {
                this.activeDayIsOpen = false;
            } else {
                this.activeDayIsOpen = true;
                this.viewDate = date;
            }
        }
    }

    // TODO : this look a bit dirty, better implement a StateMachine pattern or maybe Redux in Reactive X lib.
    handleEvent(click: String, event: Event) {
        this.eventSelected.emit({ currentAction: 2, event: event });
    }

    /**
     * Get an int representing the first day of the week. 0 = Sunday, 1= Monday...
     */
    getWeekStartsOn(): any {
        return _window().sagendaWeekStartsOn;
    }

    /**
     * Get the short version of language culture code such as "fr_CH" for Swiss-French..
     */
    getLanguageCultureShortName(): any {
        return _window().sagendaLanguageCultureShortName;
    }

    /**
     * Get the short version of language adpated for Angular
     */
    getLanguageShortName(): any {
        if (_window().sagendaLanguageCultureShortName != null) {
            switch (_window().window.sagendaIntegration) {
                case 'WordPress': return this.getLanguageShortNameForWordPressIntegration(_window().sagendaLanguageCultureShortName);
                default: return 'en';
            }
        }
        return 'en';
    }

    /**
     * Make the convertion for an intergratin with WordPress language and culture
     */
    getLanguageShortNameForWordPressIntegration(value: String): any {
        // this will solve culture matching with WordPress
        switch (_window().sagendaLanguageCultureShortName) {
            case 'ary': return 'ar-YE';
            case 'azb': return 'az-Cyrl';
            case 'bel': return 'be';
            case 'ceb': return 'ce';
            case 'de_CH_informal': return 'de-CH';
            case 'de_CH': return 'de-CH';
            case 'dzo': return 'dz';
            case 'haz': return 'ha';
            case 'oci': return 'fr'; // no support for Occitan in Angular for now - fallback to French?
            case 'rhg': return 'en'; // no support for Ruáinga in Angular for now - fallback to English
            case 'szl': return 'pl'; // no support for Silesian in Angular for now - fallback to Polish
            case 'tah': return 'ta'; // Reo Tahiti	tah ?
            case 'jv_ID': return 'en'; //  no support for Basa Jawa	(Javanese) in Angular for now - fallback to English
            case 'tl': return 'fil'; //  no support for Tagalog in Angular for now - fallback to  Filipino (Pilipino) has the code [fil]

            case 'en_US': return 'en-US-POSIX';
            default:
                break;
        }

        if (_window().sagendaLanguageCultureShortName.indexOf('_') !== -1) {
            return _window().sagendaLanguageCultureShortName.substring(0, _window().sagendaLanguageCultureShortName.indexOf('_'));
        }
        return _window().sagendaLanguageCultureShortName;
    }


}

/**
 * Return the window native object
 */
function _window(): any {
    return window;
}



