import { Component, OnInit, ViewEncapsulation } from '@angular/core';

// TODO : replace with StateMachine Pattern?
enum CurrentAction {
    date_selection = 1,
    subscription = 2,
    done = 3
}

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'],
    // encapsulation: ViewEncapsulation.Native  // Use the native Shadow DOM to encapsulate our CSS -> this will be great for css isolation to include in CMS but not very supported by navigators for now.
})
export class AppComponent {
    currentAction: CurrentAction = 1;
    calendarEvent: Event;
    objLoaderStatus: boolean;
    sagendaSuccessfullySubscribed: string;

    /**
     * Constructor
     */
    constructor() {
        this.sagendaSuccessfullySubscribed = _window().sagendaSuccessfullySubscribed;
    }

    handelCurrentAction(eventData: {currentAction: number, event: Event}) {
        this.currentAction = eventData.currentAction;
        this.calendarEvent = eventData.event;
    }
}

/**
 * Return the window native object
 */
function _window(): any {
    return window;
}



