import { BookableItem } from './../entity/BookableItem';
import { Injectable } from '@angular/core';
import { CalendarEvent } from 'angular-calendar';
import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';
import { Booking } from '../entity/Booking';
// import * as moment from 'moment';
import * as moment from 'moment-timezone';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';


const colors: any = {
    red: {
        primary: '#ad2121',
        secondary: '#FAE3E3'
    },
    blue: {
        primary: '#1e90ff',
        secondary: '#D1E8FF'
    },
    yellow: {
        primary: '#e3bc08',
        secondary: '#FDF1BA'
    },
    green: {
        primary: '#13a500',
        secondary: '#c0fdb6'
    }
};


@Injectable()
export class APIv3Service {
    // private baseUrl = 'https://sagenda-test.apphb.com/api/v3'; // TEST
    private baseUrl = 'https://sagenda.net/api/v3/'; // PROD

    private webServicesDateFormat = 'YYYY-MM-DD';

    constructor(private httpClient: HttpClient) {

    }

    // TODO : implements booking and lock
    setBooking(calendarEvent: CalendarEvent, bearerToken) {

        const myBooking = (
            {
                eventIdentifier: calendarEvent.meta.eventIdentifier,
                lockIdentifier: calendarEvent.meta.eventLockId,
                member: {
                    email: calendarEvent.meta.email,
                    firstName: calendarEvent.meta.firstName,
                    lastName: calendarEvent.meta.lastName,
                    courtesy: calendarEvent.meta.courtesy,
                    phoneNumber: calendarEvent.meta.phone,
                    description: calendarEvent.meta.description
                }
            });
        const query = myBooking;
        console.log(query);

        const headers = new HttpHeaders()
            .set('Content-Type', 'application/json')
            .set('Authorization', 'Bearer ' + bearerToken);

        const results = this.httpClient
            .post(this.baseUrl + 'events', query, {
                headers
            })
            .catch(handleError);

        return results;
    }

    /*
    Get Bearer token via sagenda token,
    @api_token : "API token as a string"
    */
    getBearerToken(api_token: string): Observable<string> {
        let body;

        body = new HttpParams()
            .set('grant_type', 'api_token')
            // .set('grant_type', 'frontend')
            // .set('api_token', '1a93439dd5d74c3fad6d7b334235f089'); // TEST
            .set('api_token', api_token); // PROD

        const headers = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');
        const bearerToken = this.httpClient
            .post(this.baseUrl + 'token', body.toString(), { headers }) // no need to add v3 for token
            .map(object => mapBearer(object))
            .catch(handleError);

        return bearerToken;
    }

    getEventLock(bearerToken, eventIdentifier: string) {
        const request = ({eventIdentifier: eventIdentifier});

        const headers = new HttpHeaders()
            .set('Content-Type', 'application/json')
            .set('Authorization', 'Bearer ' + bearerToken);

        const response = this.httpClient
            .post<string>(this.baseUrl + 'eventLocks', JSON.stringify(request), { headers })
            .catch(handleError);
        return response;
    }

    // TODO : not working...
    deleteEventLock(bearerToken, eventLockId: string) {
        const headers = new HttpHeaders()
            .set('Content-Type', 'application/json')
            // .set('Content-Type', 'application/x-www-form-urlencoded')
            .set('Authorization', 'Bearer ' + bearerToken);

        const response = this.httpClient
            .delete<string>(this.baseUrl + 'eventLocks/' + eventLockId, { headers })
            .catch(handleError);
        return response;
    }

    getEvents(date: Date, bookableItem: BookableItem, sagendaDateFormat: string, sagendaTimeFormat: string, cultureShortCode: string, bearerToken): Observable<CalendarEvent[]> {

        const headers = new HttpHeaders().set('Authorization', 'Bearer ' + bearerToken);

        let startDate = moment(date).startOf('month');
        const today = moment();
        let endDate = moment(date).endOf('month');

        if (today > startDate) {
            startDate = today;
        }

        if (today > endDate) {
            endDate = today;
        }

        const event$ = this.httpClient
            .get<CalendarEvent[]>(this.baseUrl + 'events/' + startDate.format(this.webServicesDateFormat) + '/' + endDate.format(this.webServicesDateFormat) + '/' + bookableItem.identifier, { headers })
            .map(d => mapEvents(d, bookableItem, sagendaDateFormat, sagendaTimeFormat, cultureShortCode))
            .catch(handleError);
        return event$;
    }

    getBookableItems(bearerToken): Observable<BookableItem[]> {

        const headers = new HttpHeaders().set('Authorization', 'Bearer ' + bearerToken);

        const bi$ = this.httpClient
            .get<BookableItem[]>(this.baseUrl + 'bookableItems', { headers })
            .map(mapBookableItems)
            .catch(handleError);
        return bi$;
    }

    getToken(): any {
        return _window().sagendaToken;
    }
}

function _window(): any {
    // return the native window obj
    return window;
}

function mapBookableItems(response: BookableItem[]): BookableItem[] {
    return response.map(object => toBookableItems(object));
}

function toBookableItems(item: any): BookableItem {
    const bi = <BookableItem>(
        {
            identifier: item.identifier,
            name: item.name,
            location: item.location,
            description: item.description
        });
    // console.log('Parsed BookableItems:', bi.name);
    return bi;
}

function mapBearer(object): string {
    return object.access_token;
}

function mapEvents(response: CalendarEvent[], bookableItem, sagendaDateFormat, sagendaTimeFormat, cultureShortCode): CalendarEvent[] {
    return response.map(e => toEvent(e, bookableItem, sagendaDateFormat, sagendaTimeFormat, cultureShortCode));
}

// TODO : make this working for all case : events, all day, schedule...
function toEvent(event: any, bookableItem: BookableItem, sagendaDateFormat, sagendaTimeFormat, cultureShortCode): CalendarEvent {

    moment.locale(cultureShortCode);
    let eventTitle;
    let eventStart;
    let eventEnd;
    let startMoment;
    let endMoment;
    let isAllDay = false;

    if (event.type === 'allDay') {
        const eventDateInTextWithCulture = formatEvent(event.date, sagendaDateFormat, sagendaTimeFormat);
        eventTitle = eventDateInTextWithCulture + ' (all day)'; // TODO : translate this
        isAllDay = true;
        startMoment = event.date ;
        endMoment = event.date ;
    }

    if (event.type === 'dateAndTime' || event.type === 'schedule') {
        eventStart = event.from;
        eventEnd = event.to;
        const eventStartDateInTextWithCulture = formatEvent(event.from, sagendaDateFormat, sagendaTimeFormat);
        const eventEndDateInTextWithCulture = formatEvent(event.to, sagendaDateFormat, sagendaTimeFormat);
        eventTitle = eventStartDateInTextWithCulture + ' -> ' + eventEndDateInTextWithCulture;

        const formatString = 'YYYY-MM-DDTHH:mm';
        startMoment = moment.utc(eventStart).format(formatString);
        endMoment = moment.utc(eventEnd).format(formatString);
    }
    // paid event
    if (event.payment) {
        eventTitle += ' : ' + event.payment.amount + ' ' + event.payment.currencyCode;
    }

    const s_event = <CalendarEvent>(
        {
            start: new Date(startMoment),
            end: new Date(endMoment),
            color: colors.green,
            title: eventTitle,
            allDay: isAllDay,
            meta: {
                eventIdentifier: event.identifier,
                bookableItemId: bookableItem.identifier,
                bookableItemName: bookableItem.name,
                bookableItemLocation: bookableItem.location,
                bookableItemDescription: bookableItem.description,
                type: event.type,
            }
        });

    if (event.payment) {
        s_event.meta.paymentAmount = event.payment.amount;
        s_event.meta.paymentCurrencyCode = event.payment.currencyCode;
        s_event.meta.paymentNote = event.payment.note;
    }

    console.log('Parsed events:', s_event.start + ' ' + s_event.end);
    return s_event;
}

function formatEvent(event: string, sagendaDateFormat, sagendaTimeFormat): string {
    if (event.length < 9) {
        // small hack - add a date that will not be used cause momentjs require a full date.
        return moment('June 29, 2017 ' + event).utc().format(sagendaTimeFormat);
    }

    if (event.length > 11) {
        return moment(event).utc().format(sagendaDateFormat + ' ' + sagendaTimeFormat);
    }
    // no UTC for all day event (other wise moved form 1 day!)
    return moment(event).format(sagendaDateFormat);
}

// this could also be a private method of the component class
function handleError(error: any) {
    // log error
    // const errorMsg = error.message;
    console.error(error);

    // throw an application level error
    return Observable.throw(error);
}



